<template>
    <div id="service" :style="{backgroundImage:'url('+bg+')'}">
        <!--用户信息-->
        <user></user>
        <!--佣金导航-->
        <div class="boxNr">
            <ul>
                <li>
                  <span>{{developmentNumber}}</span>
                  <p>发展用户</p>
                </li>
                <li>
                  <span>￥{{commission}}</span>
                  <p>今日佣金</p>
                </li>
                <li>
                  <span>￥{{totalCommission}}</span>
                  <p>总佣金</p>
                </li>
            </ul>
        </div>
        <div  class="bottomBox">
          <ol>
            <router-link to="/myTeam">
                <li>
                  <span><img src="./../assets/images/game85.png"></span>
                  <p>我的团队</p>
                </li>
            </router-link>
            <router-link to="/myProfit">
              <li>
                <span><img src="./../assets/images/game87.png"></span>
                <p>我的收益</p>
              </li>
            </router-link>
            <router-link to="/teamRecord">
              <li>
                <span><img src="./../assets/images/game86.png"></span>
                <p>团队记录</p>
              </li>
            </router-link>
          </ol>
          <ol>
            <router-link to="/wallet">
              <li>
                <span><img src="./../assets/images/game84.png"></span>
                <p>我的钱包</p>
              </li>
            </router-link>
            <router-link to="/client">
              <li>
                <span><img src="./../assets/images/game83.png"></span>
                <p>客服</p>
              </li>
            </router-link>
            <router-link to="/myData">
            <li>
              <span><img src="./../assets/images/game88.png"></span>
              <p>资料</p>
            </li>
            </router-link>
          </ol>
        </div>
        <!-- 底部导航菜单 -->
        <tabs :selected="selected"></tabs>
    </div>
</template>

<script>
import tabs from './../components/tabs'
import user from './../components/user'
export default {
  name: 'service',
  data () {
    return {
      selected: 'service', // 导航当前页的状态
      bg: require('./../assets/images/game1.png'), // 背景图片
      developmentNumber: 2323, // 发展人数
      commission: 4547, // 佣金
      totalCommission: 46778 // 总佣金
    }
  },
  components: {
    tabs, user
  }
}
</script>

<style scoped>
#service{background-size: 100% 100%;height: 100%;position: fixed;width: 100%;}

/*客服头像样式*/
.serverImg{position: fixed; left: 1rem; bottom: 1.6rem; width: 13rem;}
.serverImg img{width: 100%;}
.serverImg .codeImg{position: absolute;top: -70%;left: 42%;width: 11rem;}

.boxNr{display: inline-block;width: 100%;margin-top: 1rem;}
.boxNr ul{display: inline-block; height: 4rem; width: 100%;background: rgba(118, 2, 14, 0.51);}
.boxNr ul li{float: left; width: 33%; height: 4rem; border-right: 1px solid #707070;text-align: center}
.boxNr ul li span{color: #f4d983;font-size: 0.8rem;font-weight: 600;padding-top: 0.6rem;display: block;}
.boxNr ul li p{color: #b89d5e; font-size: 0.8rem;padding-top: 0.6rem;}

.bottomBox{display: inline-block;width: 100%;margin-top: 1rem;}
.bottomBox ol{display: inline-block; height: 8rem; width: 100%;}
.bottomBox ol li{float: left; width: 33%; height: 8rem;text-align: center}
.bottomBox ol li span{display: block;margin-top: 1rem;}
.bottomBox ol li span img{width: 4rem;}
.bottomBox ol li p{color: #fff; font-size: 0.8rem;padding-top: 0.6rem;}
</style>



